import ActionIcon from 'components/ActionIcon';
import ShareIcon from '@mui/icons-material/Share';
import EmailInput from 'components/EmailInput';
import { useState, useEffect } from 'react';
import PropTypes from 'prop-types';

import Dialog from '@mui/material/Dialog';
import Button from '@mui/material/Button';
import Stack from '@mui/material/Stack';
import Box from '@mui/material/Box';
import DialogTitle from '@mui/material/DialogTitle';
import Divider from '@mui/material/Divider';

import api from '../Api';

export function ShareMemoryIcon() {
  return (
    <ActionIcon icon={<ShareIcon />} label="Share" />
  );
}

export function ShareComponent({ confirm, callback, closeCallback }) {
  const ShareTitle = 'Share Memory';
  const [open, setOpen] = useState(confirm);
  useEffect(() => setOpen(confirm), [confirm]);

  const handleClose = () => {
    setOpen(false);
    closeCallback();
  };

  const handleSubmit = () => {
    handleClose();
    callback();
  };

  return (
    <Dialog fullWidth open={open} sx={{ backgroundColor: 'faded.black' }}>
      <DialogTitle>{ShareTitle}</DialogTitle>
      <Divider />
      <Box sx={{ p: 2 }}>
        <EmailInput />
        <Stack spacing={2} direction="row">
          <Button variant="outlined" onClick={handleClose}>Cancel</Button>
          <Button variant="contained" onClick={handleSubmit}>Share</Button>
        </Stack>
      </Box>
    </Dialog>

  );
}

export default async function shareMemory(bucket, authURL, memoryID, memoryShareCallback) {
  try {
    const shareResults = await api.shareMemory(bucket, authURL, memoryID);
    memoryShareCallback(shareResults);
  } catch (error) {
    /* eslint-disable no-console */
    console.log(error);
  }
}

ShareComponent.propTypes = {
  confirm: PropTypes.bool.isRequired,
  callback: PropTypes.func.isRequired,
  closeCallback: PropTypes.func.isRequired,
};
